/* PC尺寸 */
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 108px;
    padding: 0 5%;
    background-color: $color-red;

    .logo{
        width: 80px;
    }

    .menu{
        display: flex;
        justify-content: space-between;
        align-items: center;

        .headerMenu{
            color: #fff;
            font-size: 20px;
            margin: 0 28px;
            line-height: 30px;
        }
    }
}

/* 平板尺寸 */
@media (max-width: 768px){

}

/* 手機尺寸 */
@media (max-width: 414px){
    .header{
        padding: 0;
        flex-direction: column;
        height: auto;
        position: relative;

        .logo{
            display: block;
            height: 100%;
            padding: 10px 0;
        }

        .menu{
            flex-wrap: wrap;
            width: 100%;

            .headerMenu{
                width: 49.5%;
                margin: 0;
                text-align: center;
                padding: 8px 0;
                border: 1px solid #fff;
            }
        }
    }
}